import { useEffect, useState } from "@wordpress/element";
import { __ } from "@wordpress/i18n";
import { Button, Dashicon } from "@wordpress/components";
import { isUndefined } from "../helpers";
import { prefix } from "../constants";

const Repeater = (props) => {
	const [selectedPage, setSelectedPage] = useState([]);

	useEffect(() => {
		try {
			if (!isUndefined(props.value)) {
				const parsedValue =
					typeof props.value === "string"
						? JSON.parse(props.value)
						: props.value;
				setSelectedPage(parsedValue);
			}
		} catch (error) {
			console.error("value is Not a JSON response.");
		}
	}, [props.value]);

	const push = (data) => JSON.stringify([...selectedPage, data]);

	const add = (e) => {
		e.preventDefault();
		props.onChange(push(props.defaultValue || {}));
	};

	const remove = (i) => (e) => {
		e.preventDefault();
		if (window.confirm(__("Are you sure, you want to delete?"))) {
			const updatedPage = [...selectedPage];
			updatedPage.splice(i, 1);
			props.onChange(JSON.stringify(updatedPage));
		}
	};

	const handleChange = (index, key) => (value) => {
		const updatedPage = [...selectedPage];
		if (isUndefined(updatedPage[index])) {
			updatedPage[index] = {};
		}
		updatedPage[index][key] = value;

		props.onChange(JSON.stringify(updatedPage), key, index);
	};

	const addButton = (count) => {
		const { limit } = props;
		if (isUndefined(limit) || limit > count) {
			return (
				<Button isPrimary onClick={add} data-count={count}>
					{__("Add New", "rise-blocks")}
				</Button>
			);
		} else {
			// You can customize the behavior when the limit is reached
			return null;
		}
	};

	if (isUndefined(props.cb)) {
		return null;
	}

	return (
		<div className={`${prefix}-repeater-wrapper`}>
			{selectedPage.map((p, i) => (
				<div className={`${prefix}-item`} key={i}>
					<div className={`${prefix}-repeater-selects`}>
						{props.cb({handleChange}, p, i)}
					</div>
					<Button className="remove" onClick={remove(i)}>
						<Dashicon size="15" icon="trash" />
					</Button>
				</div>
			))}
			{addButton(selectedPage.length)}
		</div>
	);
};

export default Repeater;
