import { __ } from "@wordpress/i18n";
import { useEffect, useState, useRef } from "@wordpress/element";
import Popup from "reactjs-popup";
import Pagination from "../components/pagination";
import { prefix } from "../constants";
import { classnames, globalize } from "../helpers";

const getPropsValue = (props) => {
	return (
		props.value || {
			icon: "",
			page: 1,
		}
	);
};

const RenderIconSelector = (props) => {
	const [state, setState] = useState({
		allSearchedIcons: [],
		searchString: "",
		currentIcons: [],
		currentPage: null,
		totalPages: null,
	});

	const pageLimit = 16;
	const searchInput = useRef(null);

	useEffect(() => {
		searchInput.current.focus();

		return () => {
			if (searchInput && searchInput.current){
                console.log( searchInput );
                searchInput.current.blur();
            } 
		};
	}, []);

	const isSearching = () => state.searchString !== "";

	const onPageChanged = (data) => {
		const icons = isSearching() ? state.allSearchedIcons : props.icons;
		const { totalPages, currentPage } = data;
		const offset = (currentPage - 1) * pageLimit;
		const currentIcons = icons.slice(offset, offset + pageLimit);

		setState((prevState) => ({
			...prevState,
			currentPage,
			currentIcons,
			totalPages,
		}));
	};

	const onSearch = (e) => {
		const allSearchedIcons = props.icons.filter((i) => {
			let temp = i.toLowerCase().replace("fa-", "");
			return temp.match(e.target.value);
		});

		const currentIcons = allSearchedIcons.slice(0, pageLimit);

		setState((prevState) => ({
			...prevState,
			allSearchedIcons,
			searchString: e.target.value,
			currentIcons,
		}));
	};

	const onSelect = (icon) => (e) => {
		e.preventDefault();

		let page = state.currentPage;
		if (state.searchString.trim().length > 0) {
			const i = props.icons.indexOf(icon);
			page = Math.ceil((parseInt(i) + 1) / pageLimit);
		}

		props.onChange({
			icon: icon,
			page,
		});
	};

	const pageInfo = () => {
		const { currentIcons, totalPages, currentPage } = state;

		if (currentIcons.length <= 0) return;

		const Page = ({ value }) => (
			<span className={`${prefix}-bold`}>{value}</span>
		);

		return (
			<span className="search-label">
				{__("Page", "rise-block")}
				<Page value={currentPage} /> / <Page value={totalPages} />
			</span>
		);
	};

	const printIcons = () => {
		const { currentIcons } = state;
		if (currentIcons.length <= 0) return;

		return (
			<div className={`${prefix}-icon-items-wrapper`}>
				{currentIcons.map((icon, index) => (
					<div
						key={index}
						className={classnames({
							active: getPropsValue(props).icon === icon,
						})}
					>
						<a key={index} onClick={onSelect(icon)}>
							<i className={`${props.prefix} ${icon}`}></i>
						</a>
					</div>
				))}
			</div>
		);
	};

	const { allSearchedIcons } = state;
	const totalIcons = isSearching()
		? allSearchedIcons.length
		: props.icons.length;

	return (
		<div className={`${prefix}-icon-selector`}>
			<div className={`${prefix}-icon-selector-search`}>
				<input
					ref={searchInput}
					type="text"
					value={state.searchString}
					onChange={onSearch}
					placeholder={__("Search Icons", "rise-block")}
				/>
			</div>

			<div className={`${prefix}-search-label-wrap`}>
				<h2 className={`${prefix}-icon-label`}>
					<strong>{totalIcons}</strong>
					{__(" Icons", "rise-block")}
				</h2>
			</div>

			{printIcons()}

			<div className={`${prefix}-icon-selector-pagination`}>
				{pageInfo()}
				<Pagination
					// ref="child"
					initialPage={getPropsValue(props).page}
					totalRecords={totalIcons}
					pageLimit={16}
					pageNeighbours={0}
					onPageChanged={onPageChanged}
				/>
			</div>
		</div>
	);
};

const IconSelector = (props) => {
	const value = getPropsValue(props);
	return (
		<div className={`${prefix}-icon-selector`}>
			<Popup
				trigger={
					<button className={`${prefix}-icon-selector-button`}>
						<i className={`${props.prefix} ${value.icon}`}></i>
						<span className={`${prefix}-icon-selector-label`}>
							{value.icon ? __("Selected Icon", "rise-blocks") : props.label}{" "}
						</span>
						<i className="fa fa-sort-down"></i>
					</button>
				}
				position="bottom center"
				closeOnDocumentClick
			>
				<div>
					<RenderIconSelector {...props} />
				</div>
			</Popup>
		</div>
	);
};

export default IconSelector;

globalize("IconSelector", IconSelector);
