import { __ } from "@wordpress/i18n";
import { InspectorControls } from "@wordpress/block-editor";
import { Fragment } from "@wordpress/element";
import { PanelBody, BaseControl, TextControl } from "@wordpress/components";

import { PanelColor, BackgroundPosition } from "../../../custom-control";
import ImageControl from "../../../custom-control/image-control";
import Repeater from "../../../custom-control/repeater";

/* Contants */
import { faSocialIcons } from "../../../constants";
import { defaultAttrs } from "../contants";

/* Custom Components */
import HeadingToolbar from "../../../components/heading";
import IconSelector from "../../../custom-control/icon-selector";
import Typography from "../../../custom-control/typography";
import Dimension from "../../../custom-control/dimension";

const InspectorPanel = ({ attributes, setAttributes }) => {
	const {
		nameColor,
		designationColor,
		contentColor,
		socialMediaColor,
		socialMediaHoverColor,
		activeLayout,
		backgroundColor,
		nameTag,
		image,
		backgroundPosition,
		namePadding,
		designationPadding,
		nameTypography,
		designationTypography,
		contentTypography,
		socialMedia,
		socialMediaBorderRadius,
	} = attributes;

	const getColorSettings = () => {
		const commonColorSettings = [
			{
				label: __("Name", "rise-blocks"),
				value: nameColor,
				onChange: (nameColor) => setAttributes({ nameColor }),
			},
			{
				label: __("Designation", "rise-blocks"),
				value: designationColor,
				onChange: (designationColor) => setAttributes({ designationColor }),
			},
			{
				label: __("Description", "rise-blocks"),
				value: contentColor,
				onChange: (contentColor) => setAttributes({ contentColor }),
			},
			{
				label: __("Background Color", "rise-blocks"),
				value: backgroundColor,
				onChange: (backgroundColor) => setAttributes({ backgroundColor }),
			},
		];

		const rectangleLayoutColorSettings = [
			{
				label: __("Social Media", "rise-blocks"),
				value: socialMediaColor,
				onChange: (socialMediaColor) => setAttributes({ socialMediaColor }),
			},
			{
				label: __("Social Media Text Color on Hover", "rise-blocks"),
				value: socialMediaHoverColor,
				onChange: (socialMediaHoverColor) =>
					setAttributes({ socialMediaHoverColor }),
			},
		];

		return "rectangle" === activeLayout
			? [...commonColorSettings, ...rectangleLayoutColorSettings]
			: commonColorSettings;
	};

	return (
		<InspectorControls>
			{/* GENERAL SETTINGS */}
			<PanelBody title={__("General", "rise-blocks")}>
				<div className="components-base-control">
					<BaseControl label={__("Heading Tag", "rise-blocks")}>
						<HeadingToolbar
							minLevel="1"
							maxLevel="7"
							selectedLevel={nameTag}
							onChange={(nameTag) => {
								setAttributes({ nameTag });
							}}
						/>
					</BaseControl>
					<BaseControl label={__("Image", "rise-blocks")}>
						<ImageControl
							value={image}
							onSelect={(image) => setAttributes({ image })}
						/>
					</BaseControl>

					{image.url && (
						<BackgroundPosition
							value={backgroundPosition}
							onChange={(backgroundPosition) =>
								setAttributes({ backgroundPosition })
							}
						/>
					)}
				</div>
			</PanelBody>

			{/* SOCIAL MEDIA */}
			{"rectangle" === activeLayout && (
				<PanelBody
					title={__("Social Media", "rise-blocks")}
					initialOpen={false}
				>
					<Dimension
						label={__("Social Links (border radius)", "rise-block")}
						defaultValue={defaultAttrs.socialMediaBorderRadius}
						value={socialMediaBorderRadius}
						onChange={(socialMediaBorderRadius) =>
							setAttributes({ socialMediaBorderRadius })
						}
					/>

					<Repeater
						value={socialMedia}
						default={{ url: "", icon: {} }}
						limit={3}
						onChange={(socialMedia) =>
							setAttributes({ socialMedia: JSON.parse(socialMedia) })
						}
						cb={($this, data, i) => (
							<Fragment>
								<IconSelector
									label={__("Select an Icon", "rise-blocks")}
									value={data.icon}
									prefix="fa"
									icons={faSocialIcons}
									onChange={$this.handleChange(i, "icon")}
								/>
								<TextControl
									placeholder={__("URL", "rise-blocks")}
									value={data.url}
									onChange={$this.handleChange(i, "url")}
								/>
							</Fragment>
						)}
					/>
				</PanelBody>
			)}

			{/* SPACING */}
			<PanelBody title={__("Spacing", "rise-blocks")} initialOpen={false}>
				<Dimension
					label={__("Name (Margin)", "rise-block")}
					defaultValue={defaultAttrs.namePadding}
					value={namePadding}
					onChange={(namePadding) => setAttributes({ namePadding })}
				/>
				<Dimension
					label={__("Designation (Margin)", "rise-block")}
					defaultValue={defaultAttrs.designationPadding}
					value={designationPadding}
					onChange={(designationPadding) =>
						setAttributes({ designationPadding })
					}
				/>
			</PanelBody>

			{/* TYPOGRAPHY */}
			<PanelBody title={__("Typography", "rise-blocks")} initialOpen={false}>
				<Typography
					label={__("Name", "rise-block")}
					value={nameTypography}
					defaultValue={defaultAttrs.nameTypography}
					onChange={(nameTypography) => setAttributes({ nameTypography })}
				/>
				<hr />
				<Typography
					label={__("Designation", "rise-block")}
					value={designationTypography}
					defaultValue={defaultAttrs.designationTypography}
					onChange={(designationTypography) =>
						setAttributes({ designationTypography })
					}
				/>
				<hr />
				<Typography
					label={__("Description", "rise-block")}
					value={contentTypography}
					defaultValue={defaultAttrs.contentTypography}
					onChange={(contentTypography) => setAttributes({ contentTypography })}
				/>
			</PanelBody>

			{/* COLORS */}
			<PanelColor colorSettings={getColorSettings()} />
		</InspectorControls>
	);
};
export default InspectorPanel;
