import "../styles/editor.scss";

import { __ } from "@wordpress/i18n";
import { useEffect } from "@wordpress/element";
import { withInstanceId } from "@wordpress/compose";
import { RichText, useBlockProps } from "@wordpress/block-editor";

import Inspector from "./inspector";

/* plugin constants */
import { prefix } from "../../../constants";
/* current block constants */
import { editorNSaveAttrs } from "../contants";
/* parent constants */
import SocialMedia from "./social-media";
import {
	getTypoStyle,
	getDimensionStyle,
	setInitialAttrs,
	shouldShowUpgrader,
} from "../../../helpers";
import Upgrader from "../../../components/upgrader";

const Edit = (props) => {
	const { attributes, clientId, isSelected, instanceId, setAttributes } = props;

	const {
		isInit,

		activeLayout,

		image,
		backgroundPosition,
		backgroundColor,

		designation,
		name,
		content,
		nameTag,

		nameTypography,
		designationTypography,
		contentTypography,

		namePadding,
		designationPadding,

		nameColor,
		designationColor,
		contentColor,
		socialMediaColor,
		socialMediaHoverColor,

		socialMedia,
		socialMediaBorderRadius,
	} = attributes;

	/*--------------------------------------------------------*
	 * Set Initial Attributes
	 * Set the default value of attributes once. it's helps
	 * to set default value on server side too.
	 *---------------------------------------------------------*/
	if (!isInit) {
		setInitialAttrs(props, editorNSaveAttrs);
		return null;
	}

	if (shouldShowUpgrader(clientId)) {
		return (
			<div
				className={`${prefix}-profile-card wp-block-rise-blocks-profile-card`}
			>
				<Upgrader />
			</div>
		);
	}

	useEffect(() => {
		/*-------------------------------------------------------*
		 * Block_id must be  update on componentDidmount
		 * otherwise we will get same block_id while duplicating
		 * Blocks.( Parent BLocks)
		 *-------------------------------------------------------*/
		setAttributes({
			block_id: `${prefix}-wp-block-rise-blocks-profile-card-${instanceId}-${clientId}`,
		});
	}, []);

	const imageStyle = {
		backgroundImage: image ? `url('${image.url}')` : "",
		backgroundPosition,
	};

	const nameStyle = {
		...getTypoStyle(nameTypography),
		...getDimensionStyle("padding", namePadding),
		color: nameColor,
	};

	const designationStyle = {
		...getTypoStyle(designationTypography),
		...getDimensionStyle("padding", designationPadding),
		color: designationColor,
		border: "rectangle" === activeLayout ? `1px solid ${designationColor}` : "",
	};

	const contentStyle = {
		...getTypoStyle(contentTypography),
		color: contentColor,
	};

	const blockProps = useBlockProps({
		style: {
			backgroundColor: backgroundColor,
		},
		className: `${prefix}-profile-card wp-block-rise-blocks-profile-card`,
	});

	return (
		<>
			{isSelected && <Inspector {...props} />}

			<div {...blockProps}>
				<div className={`${prefix}-profile-card-image`} style={imageStyle}>
					{"url('')" === imageStyle.backgroundImage && (
						<i className="dashicons dashicons-format-image"></i>
					)}
				</div>

				<div className={`${prefix}-description-wrapper`}>
					{"rectangle" === activeLayout && (
						<div className={`${prefix}-profile-card-designation`}>
							<span
								className={`${prefix}-designatio}n`}
								style={designationStyle}
							>
								<RichText
									value={designation}
									placeholder={__("Designation", "rise-blocks")}
									onChange={(designation) => setAttributes({ designation })}
								/>
							</span>
						</div>
					)}

					<div className={`${prefix}-profile-card-title`}>
						<RichText
							style={nameStyle}
							tagName={`h${nameTag}`}
							value={name}
							placeholder={__("Name", "rise-blocks")}
							onChange={(name) => setAttributes({ name })}
						/>
					</div>

					{"circle" === activeLayout && (
						<div className={`${prefix}-profile-card-designation`}>
							<span
								className={`${prefix}-designation`}
								style={designationStyle}
							>
								<RichText
									value={designation}
									placeholder={__("Designation", "rise-blocks")}
									onChange={(designation) => setAttributes({ designation })}
								/>
							</span>
						</div>
					)}

					<div className={`${prefix}-profile-card-description`}>
						<RichText
							style={contentStyle}
							tagName="p"
							value={content}
							placeholder={__("Desctiption", "rise-blocks")}
							onChange={(content) => setAttributes({ content })}
						/>
					</div>

					{"rectangle" === activeLayout &&
						socialMedia &&
						socialMedia.length > 0 && (
							<SocialMedia
								socialMediaArr={socialMedia}
								socialMediaBorderRadius={socialMediaBorderRadius}
								socialMediaColor={socialMediaColor}
								socialMediaHoverColor={socialMediaHoverColor}
							/>
						)}
				</div>
			</div>
		</>
	);
};

export default withInstanceId(Edit);
