import { __ } from "@wordpress/i18n";
import { Fragment } from "@wordpress/element";
import { MediaUploadCheck, MediaUpload } from "@wordpress/block-editor";

import { isUndefined, globalize } from "../helpers";
import { prefix } from "../constants";
import { ImageSizeControl } from "../custom-control";

const ImageControl = (props) => {
	const defaultSize = "full";

	const getValue = () => {
		const { value } = props;

		if (typeof value === "undefined") {
			return {
				size: defaultSize,
				url: undefined,
			};
		}

		return value;
	};

	const onRender = ({ open }) => {
		const onRemove = (e) => {
			e.preventDefault();
			props.onSelect({
				...getValue(),
				url: "",
			});
		};

		const style = {};
		const { url, size } = getValue();

		if (url) {
			style.backgroundImage = `url(${url})`;
		}

		return (
			<div className={`${prefix}-image-uploader`}>
				<div className={`${prefix}-image-uploader-wrapper`}>
					<div className={`${prefix}-button-wrapper`}>
						<button
							onClick={open}
							className={`${prefix}-image-uploader-trigger`}
							style={style}
						></button>
						{url && (
							<button
								className={`${prefix}-image-uploader-delete`}
								isDestructive
								onClick={onRemove}
							>
								{__("Delete Image", "rise-blocks")}
							</button>
						)}
					</div>
				</div>

				{url && (
					<ImageSizeControl
						value={size}
						onChange={(v) => {
							onSelect(v, "size");
						}}
					/>
				)}
			</div>
		);
	};

	const onSelect = (v, type) => {
		let d;
		if ("image" === type) {
			let { size = defaultSize } = getValue();
			const { url, height, width } = isUndefined(v.sizes[size])
				? v.sizes[defaultSize]
				: v.sizes[size];

			d = {
				size,
				id: v.id,
				alt: v.alt,
				url,
				height,
				width,
				sizes: v.sizes,
			};
		} else {
			let { sizes, alt, id } = getValue();
			let size = isUndefined(sizes[v]) ? defaultSize : v;

			const { url, height, width } = sizes[size];
			d = {
				size: v,
				id,
				alt,
				url,
				height,
				width,
				sizes,
			};
		}

		props.onSelect(d);
	};

	return (
		<Fragment>
			<MediaUploadCheck>
				<MediaUpload
					gallery={false}
					multiple={false}
					onSelect={(v) => {
						onSelect(v, "image");
					}}
					allowedTypes={["image"]}
					value={props.value}
					render={onRender}
				/>
			</MediaUploadCheck>
		</Fragment>
	);
};

export default ImageControl;

globalize("ImageControl", ImageControl);
