import { prefix } from '../../../constants';
import Radium from 'radium';
import { getDimensionStyle } from '../../../helpers';

const SocialMedia = (props) => {
    const {
        socialMediaArr = [],
        socialMediaBorderRadius = {},
        socialMediaColor = '',
        socialMediaHoverColor = ''
    } = props;

    const socialMediaStyle = {
        ...getDimensionStyle('border-redius', socialMediaBorderRadius),
        border: `1px solid ${socialMediaColor}`,
        color: socialMediaColor,

        ':hover': {
            background: socialMediaColor,
            color: socialMediaHoverColor
        }
    };

    return (
        <div className={`${prefix}-social-media`} >
            {socialMediaArr.map(({ url, icon = {}, newTab = true, }, i) => {
                let target = newTab ? '_blank' : '';
                return (
                    <a
                        key={`social-media-${i}`}
                        href={url ? url : '#'}
                        target={target}
                        style={socialMediaStyle}
                        rel="noopener noreferrer" >

                        <i className={`fa ${icon.icon}`}></i>

                    </a>
                )
            })}
        </div>
    );

};

export default Radium(SocialMedia);