import { __ } from "@wordpress/i18n";
import { useEffect, useState } from "@wordpress/element";
import { prefix } from "../constants";

const LEFT_PAGE = "LEFT";
const RIGHT_PAGE = "RIGHT";

/**
 * Helper method for creating a range of numbers
 * range(1, 5) => [1, 2, 3, 4, 5]
 */
const range = (from, to, step = 1) => {
	let i = from;
	const range = [];

	while (i <= to) {
		range.push(i);
		i += step;
	}

	return range;
};

const Pagination = (props) => {
	const {
		totalRecords = null,
		pageLimit,
		pageNeighbours = 0,
		initialPage,
		onPageChanged,
	} = props;

	const [currentPage, setCurrentPage] = useState(initialPage || 1);

	const totalNumbers = pageNeighbours * 2 + 3;
	const totalBlocks = totalNumbers + 2;

	const totalPages = Math.ceil(totalRecords / pageLimit);
	const pageNeighboursAdjusted = Math.max(0, Math.min(pageNeighbours, 2));

	const handleMoveLeft = (evt) => {
		evt.preventDefault();
		gotoPage(currentPage - pageNeighboursAdjusted * 2 - 1);
	};

	const handleMoveRight = (evt) => {
		evt.preventDefault();
		gotoPage(currentPage + pageNeighboursAdjusted * 2 + 1);
	};

	const gotoPage = (page) => {
		const newPage = Math.max(0, Math.min(page, totalPages));

		const paginationData = {
			currentPage: newPage,
			totalPages,
			pageLimit,
			totalRecords,
		};

		setCurrentPage(newPage);
		onPageChanged(paginationData);
	};

	const fetchPageNumbers = () => {
		const pages = range(1, totalPages);

		if (totalPages <= totalBlocks) {
			return pages;
		}

		const startPage = Math.max(2, currentPage - pageNeighboursAdjusted);
		const endPage = Math.min(
			totalPages - 1,
			currentPage + pageNeighboursAdjusted,
		);

		let pageNumbers = range(startPage, endPage);

		if (startPage > 2) {
			const extraPages = range(startPage - totalNumbers, startPage - 1);
			pageNumbers = [LEFT_PAGE, ...extraPages, ...pageNumbers];
		}

		if (totalPages - endPage > 1) {
			const extraPages = range(endPage + 1, endPage + totalNumbers);
			pageNumbers = [...pageNumbers, ...extraPages, RIGHT_PAGE];
		}

		return [1, ...pageNumbers, totalPages];
	};

	useEffect(() => {
		gotoPage(initialPage || 1);
	}, [totalRecords, pageLimit, initialPage]);

	const pages = fetchPageNumbers();

	return (
		<>
			<nav aria-label="Icon Pagination">
				<ul className={`${prefix}-pagination`}>
					{pages.map((page, index) => {
						if (page === LEFT_PAGE)
							return (
								<li key={index} className={`${prefix}-page-item`}>
									<a
										className={`${prefix}-page-link`}
										href="#"
										aria-label="Previous"
										onClick={handleMoveLeft}
									>
										<span
											aria-hidden="true"
											className={`${prefix}-prev`}
										></span>
										<span className={`${prefix}-sr-only`}>
											{__("Previous", "rise-block")}
										</span>
									</a>
								</li>
							);

						if (page === RIGHT_PAGE)
							return (
								<li key={index} className={`${prefix}-page-item`}>
									<a
										className={`${prefix}-page-link`}
										href="#"
										aria-label="Next"
										onClick={handleMoveRight}
									>
										<span
											aria-hidden="true"
											className={`${prefix}-next`}
										></span>
										<span className={`${prefix}-sr-only`}>
											{__("Next", "rise-block")}
										</span>
									</a>
								</li>
							);

						return (
							<li
								key={index}
								className={`${prefix}-page-item${
									currentPage === page ? " active" : ""
								}`}
							>
								<a
									className={`${prefix}-page-link`}
									href="#"
									onClick={() => gotoPage(page)}
								>
									{page}
								</a>
							</li>
						);
					})}
				</ul>
			</nav>
		</>
	);
};

export default Pagination;
