import { prefix } from "../../../constants";
import { RichText, useBlockProps } from "@wordpress/block-editor";

const Save = (props) => {
	const {
		attributes: {
			block_id,
			image,
			activeLayout,
			designation,
			name,
			nameTag,
			content,
			socialMedia,
		},
	} = props;

	const Designation = () => (
		<div className={`${prefix}-profile-card-designation`}>
			<span className={`${prefix}-designation`}>
				<RichText.Content value={designation} />
			</span>
		</div>
	);

	const blockProps = useBlockProps.save({
		'id': block_id
	});

	return (
		<div {...blockProps}>
			<div className={`${prefix}-profile-card`}>
				<div className={`${prefix}-profile-card-image`}>
					{image && image.url && image.url.length === 0 && (
						<i className="dashicons dashicons-format-image"></i>
					)}
				</div>

				<div className={`${prefix}-description-wrapper`}>
					{"rectangle" === activeLayout && designation && <Designation />}

					{name && (
						<div className={`${prefix}-profile-card-title`}>
							<RichText.Content tagName={`h${nameTag}`} value={name} />
						</div>
					)}

					{"circle" === activeLayout && designation && <Designation />}

					{content && (
						<div className={`${prefix}-profile-card-description`}>
							<RichText.Content tagName="p" value={content} />
						</div>
					)}

					{activeLayout === "rectangle" &&
						socialMedia &&
						socialMedia.length > 0 && (
							<div className={`${prefix}-social-media`}>
								{socialMedia.map(({ url, icon = {}, newTab = true }, i) => {
									let target = newTab ? "_blank" : "";
									return (
										<a
											key={`social-media-${i}`}
											href={url ? url : "#"}
											target={target}
											rel="noopener noreferrer"
										>
											<i className={`fa ${icon.icon}`}></i>
										</a>
									);
								})}
							</div>
						)}
				</div>
			</div>
		</div>
	);
};

export default Save;
